.sport-container .item {
	color: #fff;
	margin: 0;
	font-size: 20px;
	width: 100%;
	border-bottom: 1px solid #fff;
	overflow-x: hidden; 
}

.sport-container-header {
	border: 1px solid green;
	background-color: yellow;
}

.sport-container-header h1 {
	text-align: center;
}

.sport-container .item:last-child {
	border-bottom: none;
}

ul {
	margin: 0;
	padding: 0;
	margin-bottom: 0 !important;
}

ul li {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.sport{
	background-color: green;
	cursor: pointer;
}

.country-wrapper {
}

.country {
	background-color: red;
}

.league {
	background-color: blue;
}

.teams {
	background-color: yellow;
	color: #000 !important;
}

.item-sports {
	display: -webkit-inline-flex;
	display: inline-flex;
	width: 100%;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-ms-align-items: center;
	-webkit-align-items: center;
	        align-items: center;
	/*padding: 15px;*/
}

.checkbox {
	width: 20px;
	display: -webkit-inline-flex;
	display: inline-flex;
}
/*.sport-table {
	background-color: red;
	color: #fff;
}
*/

.table-wrapper {
	max-height: 500px;
}

.table-sport {
	min-height: 300px;
	max-height: 800px;
	overflow-y: scroll;
	padding-right: 50px; 
}

.table-sport::-webkit-scrollbar { width: 0; }
.table-sport{ -ms-overflow-style: none; }
.table-sport{ overflow: -moz-scrollbars-none; }

.sport-table {
	text-align: center;
	/*border: 1px solid #000;*/
	overflow: hidden;
}

.sport-table-item {
	border-right: 1px solid #000;
	border-bottom: 1px solid #000;
}

.sport-table-item:first-child {
	border-left: 1px solid #000;
}

.sport-table-header {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	border-bottom: 1px solid #000;
	border-right: 1px solid #000;
	border-left: 1px solid #000;
}

.table-header-text {
	/*border-right: 1px solid #fff;*/
	text-align: center;
	font-size: 16px;
	vertical-align: middle;
	border-right: 1px solid #000;
	border-top: 1px solid #000;
}

.table-header-text:last-child {
	border-right: none;
}
